// TODO: PartialEq, Congruence 等検討

/**
 * 同値関係 [[equals]] と、異なる [[Eq]] のサブクラス間の同値関係を表現するための補助メソッド [[canEqual]] を持つ。
 *
 * 「集合 [[Eq]] 上の関係 [[equals]] が同値関係である」とは、次の条件を満たすことを言う。 [[Eq]] に属する任意の値 a, b, c について、
 *
 * - a.equals(a) であり、
 * - a.equals(b) → b.equals(a) であり、かつ
 * - a.equals(b) ∧ b.equals(c) → a.equals(c) である。
 *
 * [[Eq]] および [[Eq]] のサブクラスの集合上の関係 [[canEqual]] は同値関係である。つまり、任意の [[Eq]] または [[Eq]] のサブクラス A, B, C に属する任意の値 a, b, c について、
 *
 * - a.canEqual(a) であり、
 * - a.canEqual(b) → b.canEqual(a) であり、かつ
 * - a.canEqual(b) ∧ b.canEqual(c) → a.canEqual(c) である。
 *
 * [[Eq]] または [[Eq]] のサブクラス A, B について、 a.canEqual(b) であるときに限り、 A-B 間の同値関係が存在する。つまり、任意の [[Eq]] または [[Eq]] のサブクラス A, B に属する任意の値 a, b について、 a.equals(b) → a.canEqual(b) である。
 *
 * @implNote [[equals]] をオーバーライドする場合、 [[canEqual]] もオーバーライドしなければならない。
 */
export default abstract class Eq implements Class.Eq {
  /**
   * {this} が {that} と同値でありうるかどうかを返す。
   *
   * @implNote このメソッドをサブクラスでオーバーライドする場合、 {that} がそのクラスのインスタンスであるかどうかを返すように実装しなければならない。
   */
  protected abstract canEqual(that: Eq): boolean

  /**
   * {this} と {that} が同値かどうかを返す。
   *
   * @implNote このメソッドをオーバーライドする場合、 `this.canEqual(that) && that.canEqual(this)` でなければ FALSE を返すように実装しなければならない。
   */
  public abstract equals(that: Eq): boolean

  /**
   * @final
   */
  public isNotEqualTo(that: Eq): boolean {
    return !this.equals(that)
  }
}
